.header {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 20;
  background-color: transparent;
  transition: all 0.3s;

  &--active {
    background-color: #324541;
  }
}

.menu {
  display: flex;
  align-items: center;
  padding: 32px 0;
  border-bottom: 1px solid rgba(#fff, 0.2);

  ul {
    display: flex;
    margin-left: auto;

    li {
      margin-right: 48px;

      a {
        font-family: Causten-600, sans-serif;
        font-weight: 600;
        font-size: 16px;
        line-height: 1.1;
        text-transform: uppercase;
      }
    }
  }
}